import { createAction } from "redux-actions";
export const updateSearchAction = createAction(`modules/humans/updateSearch`, (value, list) => ({ value, list }));
export const updateSearch = (value, rawList = []) => {
    const list = rawList.filter(item => item.name.includes(value) ||
        item.notes.includes(value));
    return updateSearchAction(value, list);
};



