import React from "react";
export class HumansSearch extends React.Component {
    constructor() {
        super(...arguments);
        this.onChange = (e) => {
            this.props.updateSearch(e.target.value, this.props.list);
        };
    }
    render() {
        return (React.createElement("div", { className: "humans-list__search" },
            React.createElement("input", { type: "text", onChange: this.onChange, placeholder: "Search...", value: this.props.searchField })));
    }
}



