import { createAction } from "redux-actions";
export const createHumanAction = createAction(`modules/humans/createHumanAction`, (human) => human);
export const updateHumanAction = createAction(`modules/humans/updateHumanAction`, (human) => human);
export const createOrUpdateHuman = (human) => {
    return human.id !== 0 ? updateHumanAction(human) : createHumanAction(human);
};



