import qs from "query-string";
import { handleActions } from "redux-actions";
import { updateSearchAction } from "../actions/humans/updateSearch.action";
import { updateSelectedHumanAction } from "../actions/humans/updateSelectedHuman.action";
import { createHumanAction, updateHumanAction } from "../actions/humans/createOrUpdateHuman.action";
import { removeHumanAction } from "actions/humans/deleteHuman.action";
const urlData = {};
const parsed = qs.parse(location.search);
if (parsed.user) {
    urlData.selectedHuman = Number(parsed.user);
}
const initialState = Object.assign({ list: [
        {
            id: 1,
            name: "John Doe",
            notes: `He is soooo strange Q`
        }, {
            id: 2,
            name: "Janette Doe",
            notes: `She is wife of mister #1?`
        }
    ], search: "", selectedHuman: 0, searchList: [] }, JSON.parse(localStorage.getItem("humans")), urlData);
export const humans = handleActions({
    [createHumanAction]: (state, action) => (Object.assign({}, state, { list: [
            ...state.list,
            Object.assign({}, action.payload, { id: Number((state.list[state.list.length - 1] || { id: 0 }).id) + 1 })
        ], selectedHuman: Number((state.list[state.list.length - 1] || { id: 0 }).id) + 1 })),
    [updateHumanAction]: (state, action) => (Object.assign({}, state, { list: [
            ...state.list.filter(item => item.id !== action.payload.id),
            action.payload
        ] })),
    [updateSelectedHumanAction]: (state, action) => (Object.assign({}, state, { selectedHuman: action.payload })),
    [updateSearchAction]: (state, action) => (Object.assign({}, state, { searchList: action.payload.list, search: action.payload.value })),
    [removeHumanAction]: (state, action) => (Object.assign({}, state, { list: [
            ...state.list.filter(item => item.id !== action.payload)
        ] }))
}, initialState);



