import React from "react";
import cx from "classnames";
import { HumansSearch } from "./Humans.search";
export class HumansList extends React.PureComponent {
    constructor() {
        super(...arguments);
        this.updateSelectedHuman = (id) => () => {
            this.props.updateSelectedHuman(id);
        };
        this.onRemove = (id) => (e) => {
            this.props.removeHuman(id);
            e.stopPropagation();
        };
    }
    render() {
        const { selectedHuman = {
            id: 0
        }, list = [], searchField = "", searchList = [], updateSearch } = this.props;
        const final_list = searchField === "" ? list : searchList;
        return (React.createElement("div", { className: "humans-list" },
            React.createElement(HumansSearch, { updateSearch: updateSearch, searchField: searchField, list: list }),
            final_list.map(item => React.createElement("div", { className: cx("humans-list__item", { "active": item.id === selectedHuman.id }), onClick: this.updateSelectedHuman(item.id) },
                React.createElement("div", { children: item.name }),
                React.createElement("div", { className: "x-mark", onClick: this.onRemove(item.id), children: "X" }))),
            React.createElement("div", { className: "humans-list__item add", onClick: this.updateSelectedHuman(0) }, "+ Add new")));
    }
}



